import Connection from '../../controller/Connection'
import Devices from '../../controller/Devices'
const clone = require('rfdc')()

export default 'Zigbee'
export const namespace = 'zigbee'

export const deviceObjectType = 'device';


/*
 * State Mapping
 */
const STATE_MAPPING = {

	// lights
	"light": {
		"power": {
			"state": ".state",
			"action": ".state"
		},
		"level": {
			"state": ".brightness",
			"action": ".brightness"
		},
		"colorTemperature": {
			"state": ".colortemp",
			"action": ".colortemp"
		},
		"rgb": {
			"state": ".color",
			"action": ".color"
		}
	}
}


/**
 *
 *
 * @param	{Object}	deviceStructure
 * @param	{String}	deviceStructure.root		root state
 * @param	{Array}		deviceStructure.states		all states
 * @param	{Object}	deviceStructure.values		all states with its value
 * @return	{Object}	device
 */
export function parse(deviceStructure, options) {
	const socket = Connection.getConnection;
	
	return new Promise(resolve => {
		
		socket.getObject(deviceStructure.root).then(obj => {
			
			let device = {
				'name': obj.common.name,
				'function': 'other',
				'states': {
					...clone(STATE_MAPPING['light']),
					
					// general
					'reachability': '.available',
					'link_quality': '.link_quality',
					'battery': '.battery',
					
					// sensor
					'humidity': '.humidity',
					'temperature': '.temperature',
					'illumination': '.illuminance',
					'pressure': '.pressure',
					
					// motion detector
					'motion': '.occupancy',
					
					// water / smoke detector
					'alarm': '.detected',
					'test': '.selftest',
					
					// door / window
					'door': '.opened',
				}
			}
			
			// validate states
			device.states = Devices.validateStates(device.states, deviceStructure);
			
			// detect function (by states)
			if (device.states.level !== undefined || device.states.colorTemperature !== undefined || device.states.rgb !== undefined || device.states.hue !== undefined) {
				device.function = 'light';
			}
			else if (device.states.door !== undefined) {
				device.function = 'door';
			}
			else if (device.states.motion !== undefined) {
				device.function = 'motion';
			}
			else if (device.states.detected !== undefined && device.states.test !== undefined) {
				device.function = 'smoke';
			}
			else if (device.states.detected !== undefined && device.states.test === undefined) {
				device.function = 'sensor';
			}
			else if (device.states.humidity !== undefined || device.states.temperature !== undefined || device.states.illumination !== undefined || device.states.pressure !== undefined) {
				device.function = 'sensor';
			}
			
			
			// resolve
			resolve(device);
		});
	});
}