import React from 'react'
import i18n from '../../controller/i18n'
import { sortArrayWithObjects } from '../../helpers/utils'

import MenuItem from '@material-ui/core/MenuItem'

import Icon from '../Icon'
import Text from './Text'


/*
 *
 */
export default function InputSelect(props) {
	let { selection, sortOptionsByKey, ...otherProps } = props;
	
	// val is callback
	let val = typeof props.val === 'function' ? props.val() : props.val;
	
	if (sortOptionsByKey) {
		selection = sortArrayWithObjects(selection, typeof sortOptionsByKey === 'boolean' ? '_sort' : sortOptionsByKey);
	}
	
	let value = '';
	const menuOptions = selection.map(option => {
		const key = option._key ? option._key : Object.keys(option).shift();
		
		if (key === val) {
			value = val;
		}
		
		return (
			<MenuItem key={'MenuItem-' + otherProps.id + '-' + key} value={key}>
				{option['icon'] && <Icon icon={option['icon']} customClassNames="marginRight" />}
				{option._translated === true ? option[key] : i18n.t(option[key])}
			</MenuItem>
		);
	});
	
	return (
		<Text
			{...otherProps}
			val={value}
			select={true}
			>
			
			{menuOptions}
		</Text>
	);
}