
export default {
	"_any": {
		"icon": "mdi-thermometer"
	},
	"temperature": {
		"value": val => val.toFixed(1),
		"unit": " C",
		"icon": "mdi-thermometer"
	},
	"humidity": {
		"unit": " %"
	},
	"illumination": {
		"unit": " lux",
		"icon": "mdi-brightness-7"
	},
	"pressure": {
		"unit": " hPa",
		"icon": "mdi-air-purifier"
	}
}