import React from 'react'
import clsx from 'clsx'

import Log from '../controller/Log'
import Settings from '../controller/Settings'

import Card from '@material-ui/core/Card'
import CardHeader from '@material-ui/core/CardHeader'
import CardContent from '@material-ui/core/CardContent'
import Avatar from '@material-ui/core/Avatar'
import Typography from '@material-ui/core/Typography'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles(theme => ({
	card: {
		margin: theme.spacing(1, 1, 0, 0),
		[theme.breakpoints.down('md')]: {
			margin: theme.spacing(0.8, 0.8, 0, 0.4)
		}
	},
	cardHeaderNoPadding: {
		padding: '0 !important',
		margin: '0 !important',
	},
	cardAvatar: props => ({
		backgroundColor: theme.palette.primary.main,
		color: '#fff',
		fontSize: '24px',
		...props
	}),
	cardContent: {
		padding: theme.spacing(0, 2),
		[theme.breakpoints.down('md')]: {
			padding: theme.spacing(0)
		}
	}
}));


export default React.memo((props) => {
	Log._('Jarvis', [ 'RENDER Widget', props ], 'development');
	const { id, fullscreen, title, icon, iconStyle } = props;
	const classes = useStyles(iconStyle);
	
	return (
		<Card
			id={id}
			className={classes.card}
			>
			
			{fullscreen !== true && <CardHeader
				avatar={icon !== null &&
					<Avatar variant="rounded" classes={{ root: classes.cardAvatar }}>
						{React.createElement('span', { 'className': 'mdi mdi-' + ((icon && icon.replace('mdi-', '')) || Settings.getSetting('defaultBoxIcon') || 'home') })}
					</Avatar>
				}
				title={title &&
					<Typography variant="h5" component="h2">
						{title}
					</Typography>
				}
			/>}
			<CardContent className={clsx(fullscreen ? classes.cardHeaderNoPadding : classes.cardContent)}>
				{props.children}
			</CardContent>
		</Card>

	);
});
