import React from 'react'

import Inputs from '../../components/Inputs'


export default {
	devices: ['device'],
	devicesStates: {
		selectable: ['primaryStateKey']
	},
	DeviceBodyConfiguration
}

/*
 *
 */
function DeviceBodyConfiguration(props) {
	const { device, options, onChange, elements = {} } = props;
	
	if (options.type !== 'device') {
		return <Inputs.Fieldset>
			<Inputs.FieldsetItem>
				{elements.Icon} {elements.Device}
			</Inputs.FieldsetItem>
		</Inputs.Fieldset>
	}
	
	return (
		<Inputs.Fieldset key={'Device-' + (device.id || options.deviceId) + '-Body'}>
			<Inputs.FieldsetItem container>
				{elements.Icon} {elements.Device}
			</Inputs.FieldsetItem>
			
			<Inputs.FieldsetItem columns={12} noPadding>
				<Inputs.Text
					key='label'
					id='label'
					width={150}
					
					placeholder={device.name}
					val={options.label || ''}
					label='Device Label'
					onChange={onChange}
					/>
				
			</Inputs.FieldsetItem>
		</Inputs.Fieldset>
	)
}