import React, { useState, useEffect, useRef } from 'react'
import clsx from 'clsx'

import Button from '@material-ui/core/Button'
import Checkbox from '@material-ui/core/Checkbox'
import FormControl from '@material-ui/core/FormControl'
import IconButton from '@material-ui/core/IconButton'
import Input from '@material-ui/core/Input'
import InputAdornment from '@material-ui/core/InputAdornment'
import MenuItem from '@material-ui/core/MenuItem'
import Select from '@material-ui/core/Select'
import Slider from '@material-ui/core/Slider'
import Switch from '@material-ui/core/Switch'

import i18n from '../../controller/i18n'
import Log from '../../controller/Log'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => ({
	component: {
		padding: theme.spacing(2, 0, 2, 0),
		position: 'relative',
		zIndex: 99
	},
	sliderMarkLabel: {
		[theme.breakpoints.down('md')]: {
			display: 'none',
			'&:nth-child(5)': { display: 'block' },
			'&:nth-child(15)': { display: 'block' },
			'&:nth-child(25)': { display: 'block' }
		}
	},
	primaryAction: props => ({
		...props
	}),
	secondaryAction: props => ({
		color: '#999',
		fontSize: '14px',
		//...props
	}),
	IconButtonAction: {
		width: 32
	}
}));


/*
 * ACTION
 *
 */
function State(props) {
	const { device, state, stateKey, stateVal } = props;
	const classes = useStyles(device.getStyle('state', stateKey, stateVal));
	
	// detect HTML contents
	let stateValue = (state && state.value) || '';
	if (typeof stateValue === 'string' && stateValue.indexOf('<') > -1 && stateValue.indexOf('>') > -1) {
		return (
			<div className={clsx((props.primary && classes.primaryAction) || (props.secondary && classes.secondaryAction))}>
				<div dangerouslySetInnerHTML={{ __html: state.value }} />
			</div>
		);
	}
	
	// text contents
	else if (stateValue) {
		return (
			<span className={clsx((props.primary && classes.primaryAction) || (props.secondary && classes.secondaryAction))}>
				{i18n.t(stateValue)} {state && state.unit}
			</span>
		);
	}
	
	return null;
}

function SwitchAction(props) {
	const { device, stateKey, stateVal, variant, onChange: parentOnChange } = props;
	
	const onChange = (e) => {
		setChecked(e.target.checked);
		device.setDeviceState(stateKey, e.target.checked).catch(({ error }) => Log._('Function (defaults)', error.message, 'error', true, error.stack));
	}
	
	const [checked, setChecked] = useState(stateVal);
	useEffect(() => setChecked(stateVal), [stateVal]);
	
	// CheckboxAction
	if (variant === 'checkbox') {
		return (
			<Checkbox
				checked={checked}
				onChange={parentOnChange || onChange}
				value={checked !== true}
				color="primary"
				/>

		);
	}
	
	// SwitchAction
	return (
		<Switch
			checked={checked}
			onChange={parentOnChange || onChange}
			value={checked !== true}
			color="primary"
			/>

	);
}

function CheckboxAction(props) {
	return SwitchAction({ ...props, variant: 'checkbox' });
}

function IconButtonAction(props) {
	const { device, stateKey, stateVal } = props;
	const classes = useStyles();
	
	const [state,setState] = useState(stateVal === true || stateVal > 0);
	const onClick = () => {
		setState(true);
		device.setDeviceState(stateKey, true).catch(({ error }) => Log._('Function (defaults)', error.message, 'error', true, error.stack));
	}
	
	//
	useEffect(() => {
		setState(stateVal === true || stateVal > 0);
	}, [stateVal]);
	
	// show state
	if (state === true) {
		return <State {...props} onChange={null} primary />
	}
	
	// show action
	const icon = 'power';
	return (
		<IconButton
			classes={{ root: classes.IconButtonAction }}
			color="primary"
			component="span"
			onClick={onClick}
			>
			
			<span className={'mdi mdi-' + icon}></span>
		</IconButton>
	);
}

function InputAction(props) {
	const { device, state, stateKey } = props;
	
	// parse value
	const parseValue = val => dataType.current === 'number' ? (parseInt(val) || 0) : (val || '');
	
	// states
	const dataType = useRef(typeof state.value);
	const [value,setValue] = useState(parseValue(state.value));
	const [sendValue,setSendValue] = useState(parseValue(state.value));
	
	// change from outside
	useEffect(() => {
		setValue(parseValue(state.value));
	}, [state.value]);
	
	// change from inside
	const onChange = (event, set = false) => setValue(parseValue(event.target.value));
	
	// set value
	const onSet = event => {
		let val = parseValue(event.target.value);
		
		// if the value has been send already (e.g. via Enter), do not send it again onBlur
		if (sendValue === val) {
			return false;
		}
		
		setSendValue(val);
		device.setDeviceState(stateKey, val).catch(({ error }) => Log._('Function (defaults)', error.message, 'error', true, error.stack));
	}
	
	return (
		<Input
			onChange={onChange}
            endAdornment={state.unit && <InputAdornment position="end">{state.unit}</InputAdornment>}
			size="small"
			value={value}
			inputProps={{
				onKeyPress: e => e.key === 'Enter' && onSet(e),
				onBlur: onSet,
				size: Math.min(Math.ceil(value.toString().length*1,3, 15))
			}}
			/>
	);
}

function ButtonAction(props) {
	const { device, stateKey, stateVal } = props;
	
	const [state,setState] = useState(stateVal === true || stateVal > 0);
	const onClick = () => {
		setState(true);
		device.setDeviceState(stateKey, true).catch(({ error }) => Log._('Function (defaults)', error.message, 'error', true, error.stack));
	}
	
	//
	useEffect(() => {
		setState(stateVal === true || stateVal > 0);
	}, [stateVal]);
	
	// show state
	if (state === true) {
		return <State {...props} onChange={null} primary />
	}
	
	// show action
	const icon = 'power';
	return (

<Button
	variant="outlined"
	color="primary"
	size="small"
	startIcon={<span className={'mdi mdi-' + icon}></span>}
	onClick={onClick}
	>
	
	{i18n.t('turn on')}
</Button>

	);
}

function DropdownAction(props) {
	const { device, stateKey, stateVal } = props;
	
	const onChange = (e, option) => {
		let value = Number.isNaN(parseInt(e.target.value)) ? e.target.value : parseInt(e.target.value);
		device.setDeviceState(stateKey, value).catch(({ error }) => Log._('Function (defaults)', error.message, 'error', true, error.stack));
	}
	
	const options = (device.states[stateKey] && device.states[stateKey].display) || null;
	const MenuItems = options && Object.keys(options).map(key => <MenuItem key={'MenuItem-' + device.id + '-' + stateKey + '-' + key} value={key}>{options[key]}</MenuItem>);
	
	return !options ? <span>{i18n.t('Display attribute of state %s must be defined').replace('%s', stateKey)}!</span> : (
		<FormControl variant="outlined" size="small">
			<Select
				value={stateVal}
				onChange={onChange}
				>
				
				{MenuItems}
			</Select>
		</FormControl>
	);
}


/*
 * BODY
 *
 */
function Label(props) {
	const { label, br } = props;
	const linebreak = br || false;
	
	return (
	
<React.Fragment>
	{label}
	{linebreak && <br />}
</React.Fragment>
	
	);
}

function Component(props) {
	const { title, children } = props;
	const classes = useStyles();
	
	return (

<React.Fragment>
	<Label label={title} />
	<div className={classes.component}>
		{children}
	</div>
</React.Fragment>

	);
}

function LastChangeBody(props) {
	let { title, state } = props;
	//const classes = useStyles();
	
	return (<>
		{title}
		<p className="MuiTypography-root MuiListItemText-secondary MuiTypography-body2 MuiTypography-colorTextSecondary MuiTypography-displayBlock">{state.timeChanged}</p>
	</>);
}

function LevelBody(props) {
	let { device, stateKey, stateVal, title, min, max, step, markSteps } = props;
	const classes = useStyles();
	
	
	const [level, setLevel] = useState(stateVal);
	useEffect(() => setLevel(stateVal), [stateVal]);
	
	const onChange = (event, val) => setLevel(val);
	const onChangeCommitted = (event, val) => {
		setLevel(val);
		device.setDeviceState(stateKey, val).catch(({ error }) => Log._('Function (defaults)', error.message, 'error', true, error.stack));
	}
	
	// default settings
	min = min || 0;
	max = max || 100;
	step = step || 1;
	markSteps = markSteps || 11; // 0 to 10
	let markStep = ((max-min)/(markSteps-1)) || 10;
	return (

<Component title={title || stateKey }>
	<Slider
		classes={{ markLabel: classes.sliderMarkLabel }}
		onChange={onChange}
		onChangeCommitted={onChangeCommitted}
		value={level}
		valueLabelDisplay="auto"
		marks={[...Array(markSteps).keys()].map(number => ({ 'value': min+number*markStep, 'label': min+number*markStep }))}
		min={min}
		max={max}
		step={step}
	/>

</Component>

	);
}


export default {
	components: {
		Label,
		Component
	},
	body: {
		LastChangeBody,
		LevelBody
	},
	action: {
		State,
		SwitchAction,
		CheckboxAction,
		InputAction,
		ButtonAction,
		IconButtonAction,
		DropdownAction
	}
}